.typeEffect {
  font-family: "Oswald", sans-serif;
  color: #3629d2;
  border-right: black;
  animation: blinking 900ms infinite;
}

@keyframes blinking {
  0% {
    border-color: black;
  }

  50% {
    border-color: transparent;
  }
}

