.nav {
  background-color: rgb(255, 255, 255, 0.75);
}

#nav-Nadia {
  border-radius: 4px;
  padding: 5px;
  width: 150px;
}

.projects {
  font-family: "Merriweather", serif;
  flex-wrap: wrap;
}

.project-row {
  font-size: x-large;
}

.project-row:nth-child(even) {
  background-color: rgb(255, 245, 238, 0.75);
  text-align: center;
  word-wrap: break-word;
  padding: 1em;
}

.project-row:nth-child(odd) {
  background-color: rgb(255, 235, 205, 0.75);
  text-align: center;
  word-wrap: break-word;
  padding: 1em;
}

.projects h2 {
  background-color: rgb(190, 227, 215, 0.85);
  padding: 1em;
  border-radius: 55px;
}

/* reviews */
.review-list-test {
  display: grid;
  grid-template-columns: 2fr 2fr;
  gap: 10px;
  text-align: left;
}

.review-details-test {
  border-radius: 4px;
  margin: 20px auto;
  padding: 20px;
  position: relative;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.05);
}

.review-details-test h2 {
  margin: 0 0 10px 0;
}

.reviews {
  background: hsl(216, 98%, 52%, 35%); /*  35% opaque background only */
  padding: 2em;
  align-items: center;
  text-align: justify;
  border-radius: 55px;
}
.create-review {
  align-items: center;
  text-align: center;
  background-color: hsl(45, 100%, 51%, 45%);
  border-radius: 55px;
}
.create-review label {
  text-align: left;
  display: block;
}

.create-review input,
.create-review textarea {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
  border-radius: 10px;
}

.create-review button {
  background: #3629d2;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

/* end reviews  */

#summary {
  background-color: rgb(190, 227, 215, 0.5);
  padding: 2em;
  border-radius: 55px;
}
#summary h5 {
  text-decoration-line: underline;
}

.not-found {
  background-color: rgb(190, 227, 215, 0.85);
  padding: 1em;
  border-radius: 55px;
}

#heart {
  border-radius: 4px;
  padding: 2px;
  width: 50px;
}

#monster {
  border-radius: 4px;
  padding: 2px;
  width: 450px;
}

